@import "../../styles/styles.scss";

.footer {
  position: relative;
  background-color: white;
  z-index: 1200;
  padding: rem(20) 0;

  &--deactivate {
    display: none;
  }

  &__container {
    padding-top: 20px;

    p {
      letter-spacing: 0.008px;
      color: $color_main__text;
      margin-top: 0;

    }
  }

  &__copyright {
    display: flex;
    gap: 5px;

    @include large {
      flex-direction: column;
      gap: 10px;
    }
    &-s {
      @include large {
        display: none;
      }
    }

    a {
      color: $color_main__text;

      &:hover {
        color: $color__success;
      }
    }
  }

  &__syd {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 0;
  }
}
